body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

main h1 {
  text-align: center;
}
footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 12px;
  font-weight: 700;
  background-color: #111;
  color: #b9b9b9;
  padding: .5rem 1rem;
}

footer a {
  text-decoration: none;
  color: #b9b9b9;
  margin-top: .25rem;
}

footer a:hover {
  color: white;
}

.footer_contacts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
}
header {
  background-color: rgba(0,0,0, 0.85);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
}

header img {
  position: absolute;
  left: 1rem;
}

.head_logo {
  width: 168px;
}
